import { Component, OnInit, Inject, Renderer2, ElementRef, ViewChild } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { DOCUMENT } from '@angular/common';
import { Location } from '@angular/common';

import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/operator/filter';

import { NavbarComponent } from './components/navbar/navbar.component';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
    public showScroller = false;
    private _router: Subscription;
    @ViewChild(NavbarComponent) navbar: NavbarComponent;

    constructor(
        private renderer: Renderer2,
        private router: Router,
        @Inject(DOCUMENT)
        private document: any,
        private element: ElementRef,
        public location: Location) {
    }

    ngOnInit() {
        const navbar: HTMLElement = this.element.nativeElement.children[0].children[0];
        this._router = this.router.events.filter(event => event instanceof NavigationEnd).subscribe((event: NavigationEnd) => {
            if (window.outerWidth > 991) {
                window.document.children[0].scrollTop = 0;
            } else {
                window.document.activeElement.scrollTop = 0;
            }
            this.navbar.sidebarClose();
        });
        this.renderer.listen('window', 'scroll', () => {
            const winScroll = window.scrollY;
            const height = window.document.documentElement.scrollHeight - window.document.documentElement.clientHeight;
            const scrolled = (winScroll / height) * 100;
            if (winScroll > 150 || window.pageYOffset > 150) {
                navbar.classList.remove('navbar-transparent');

                this.showScroller = this.isArticle() ? true : false;
                if (this.showScroller) {
                    if (window.document.getElementById("myBar")) {
                        window.document.getElementById("myBar").style.width = scrolled + "%";
                    }
                }
            } else {
                navbar.classList.add('navbar-transparent');
                this.showScroller = false;
            }
        });
        const ua = window.navigator.userAgent;
        const trident = ua.indexOf('Trident/');
        let version;
        if (trident > 0) {
            // IE 11 => return version number
            const rv = ua.indexOf('rv:');
            version = parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
        }
        if (version) {
            var body = document.getElementsByTagName('body')[0];
            body.classList.add('ie-background');
        }
    }
    removeFooter() {
        let titlee = this.location.prepareExternalUrl(this.location.path());
        titlee = titlee.slice(1);
        if (titlee === 'signup' || titlee === 'nucleoicons') {
            return false;
        } else {
            return true;
        }
    }
    isArticle() {
        let titlee = this.location.prepareExternalUrl(this.location.path());
        if (titlee.charAt(0) === '#') {
            titlee = titlee.slice(1);
        }
        if (titlee.includes('read-article')) {
            return true;
        } else {
            return false;
        }
    }
}
